import { NextResponse } from "next/server";

export async function POST(req: Request) {
  try {
    const { phone, ticketUrl, bookingId, name, date } = await req.json();

    if (!phone || !ticketUrl) {
      return NextResponse.json(
        { error: "phone and ticketUrl are required" },
        { status: 400 },
      );
    }

    const cleanPhone = phone.replace(/\D/g, "");

    const response = await fetch(
      `https://graph.facebook.com/v19.0/${process.env.WHATSAPP_PHONE_NUMBER_ID}/messages`,
      {
        method: "POST",
        headers: {
          Authorization: `Bearer ${process.env.WHATSAPP_TOKEN}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          messaging_product: "whatsapp",
          to: cleanPhone,
          type: "document",
          document: {
            link: ticketUrl,
            filename: `${bookingId || "ticket"}.pdf`,
            caption: `üéüÔ∏è Your Ticket\n${name ? `Name: ${name}\n` : ""}${bookingId ? `Booking: ${bookingId}\n` : ""}${date ? `Date: ${date}` : ""}`.trim(),
          },
        }),
      },
    );

    const data = await response.json();

    if (!response.ok) {
      return NextResponse.json(data, { status: response.status });
    }

    return NextResponse.json(data);
  } catch {
    return NextResponse.json(
      { error: "Failed to send document" },
      { status: 500 },
    );
  }
}
